* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.container {
  width: calc(100% - 2rem);
  margin: 0 1rem;
  padding-top: 1rem;
  @media screen and (max-width: 768px) {
    width: 100%;
    margin: 0;
    padding-top: 0;
  }
  .fullWidth {
    width: 100%;
    margin: 0;
  }
}
